.standardHeader {
  display: flex;
  place-content: center;
}
.standardHeader nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 85%;
}
.standardHeader nav .logoContainer {
  display: block;
  width: 80px;
}
.standardHeader nav .navLinks {
  display: flex;
}
.standardHeader nav .navLinks li {
  list-style: none;
  transition: color 0.3s;
  padding-left: 50px;
}
.standardHeader nav .navLinks li a {
  font-size: 1.125em;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
}
.standardHeader nav .navLinks li a:hover {
  color: #ff5722;
}
.standardHeader nav .navLinks li.active a {
  color: #ff5722;
}
.standardHeader nav button {
  all: unset;
  display: none;
}
.standardHeader nav .screenDarkener {
  display: block;
  position: fixed;
  visibility: collapse;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: black;
  opacity: 0;
  transition: visibility 0s, opacity 300ms;
}
@media screen and (max-width: 800px) {
  .standardHeader {
    background-color: black;
  }
  .standardHeader nav {
    width: 90%;
  }
  .standardHeader nav .logoContainer {
    width: 60px;
    margin: auto;
  }
  .standardHeader nav .sidenavTrigger {
    all: unset;
    position: absolute;
    cursor: pointer;
    color: white;
  }
  .standardHeader nav .sidenavTrigger i {
    font-size: 1.875em;
  }
  .standardHeader nav .navLinks {
    display: block;
    position: fixed;
    margin: 0;
    padding: 0;
    width: 300px;
    top: 0;
    bottom: 0;
    z-index: 10;
    left: -300px;
    background-color: #111111;
    transition: left 300ms;
  }
  .standardHeader nav .navLinks li {
    padding: 0;
    transition: background-color 0.3s;
  }
  .standardHeader nav .navLinks li a {
    display: block;
    box-sizing: border-box;
    padding: 20px;
    width: 300px;
  }
  .standardHeader nav .navLinks li.active {
    background-color: rgba(0, 0, 0, 0.2);
  }
  .standardHeader nav .navLinks li.active a {
    color: white;
  }
  .standardHeader nav .navLinks li:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .standardHeader nav .navLinks li:hover a {
    color: white;
  }
}

a {
  color: #212121;
  text-decoration: underline;
}
a:visited {
  color: #616161;
}

h1 {
  font-weight: 300;
  font-size: 6em;
}

h2 {
  font-weight: 300;
  font-size: 3.75em;
}

h3 {
  font-weight: 400;
  font-size: 3em;
}

h4 {
  font-weight: 400;
  font-size: 2.125em;
}

h5 {
  font-weight: 400;
  font-size: 1.5em;
}

h6 {
  font-weight: 500;
  font-size: 1.25em;
}

body {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 100%;
  font-display: swap;
  margin: 0;
  min-height: 100vh;
  background-image: url("../images/power.webp");
  background-color: #212121;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  display: grid;
  grid: 100px auto/auto;
  grid-template-areas: "header" "main";
}
@media screen and (max-width: 800px) {
  body {
    grid: 80px auto/auto;
    grid-template-areas: "header" "main";
  }
}

header {
  grid-area: header;
}

main {
  grid-area: main;
}

img {
  width: 100%;
  height: auto;
}

main {
  place-self: center;
}

.presentation {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}
.presentation section {
  margin: 25px;
  flex-basis: 20%;
}
.presentation .presentationPhoto {
  min-width: 250px;
}
.presentation .presentationPhoto img {
  border-radius: 50%;
}
.presentation .presentationText {
  color: white;
}
.presentation .presentationText p, .presentation .presentationText h1 {
  font-weight: 500;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 20px;
}
.presentation .presentationText h1 {
  font-size: 3.75em;
  color: #ff5722;
}
.presentation .presentationText p {
  font-size: 1.5625em;
}